import CopyToClipboard from "react-copy-to-clipboard";
import DOMPurify from "dompurify";
import PropTypes from "prop-types";
import dayjs from "dayjs";
import hljs from "highlight.js/lib/core";
import humanizeUrl from "humanize-url";
import { marked } from "marked";
import prettyBytes from "pretty-bytes";
import { useState } from "react";

dayjs.extend(require("dayjs/plugin/relativeTime"));

hljs.registerLanguage(
  "plaintext",
  require("highlight.js/lib/languages/plaintext")
);
hljs.registerLanguage("c", require("highlight.js/lib/languages/c"));
hljs.registerLanguage("cpp", require("highlight.js/lib/languages/cpp"));
hljs.registerLanguage("json", require("highlight.js/lib/languages/json"));
hljs.registerLanguage("ini", require("highlight.js/lib/languages/ini"));
hljs.registerLanguage("python", require("highlight.js/lib/languages/python"));
hljs.registerLanguage("shell", require("highlight.js/lib/languages/shell"));

marked.setOptions({
  renderer: new marked.Renderer(),
  highlight: function (code, lang) {
    const language = hljs.getLanguage(lang) ? lang : "plaintext";
    return hljs.highlight(code, { language }).value;
  },
  langPrefix: "hljs language-",
});

function Humanize(props) {
  if (props.type === "time") {
    const djs = dayjs(props.value);
    return (
      <time dateTime={props.value} title={djs.toString()}>
        {props.format ? djs.format(props.format) : djs.from()}
      </time>
    );
  }
  if (props.type === "url") {
    return humanizeUrl(props.value);
  }
  if (props.type === "size") {
    return prettyBytes(props.value);
  }
  if (props.type === "counter") {
    try {
      return new Intl.NumberFormat("en-US").format(props.value);
    } catch (err) {
      return props.value;
    }
  }
  if (props.type === "markdown") {
    return (
      <div
        dangerouslySetInnerHTML={{
          __html: DOMPurify.sanitize(marked.parse(props.value)),
        }}
      />
    );
  }
  if (props.type === "original-version") {
    // https://github.com/platformio/platformio-core/blob/develop/platformio/package/version.py#L45
    const versionParts = props.value.split(".");
    if (versionParts.length !== 3) return props.value;
    const rawVersion = versionParts[1];
    if (parseInt(rawVersion) <= 99) return props.value;
    const originalVersion =
      parseInt(rawVersion) <= 9999
        ? `${parseInt(rawVersion.slice(0, -2))}.${parseInt(
            rawVersion.slice(-2)
          )}`
        : `${rawVersion.slice(0, -4)}.${parseInt(
            rawVersion.slice(-4, -2)
          )}.${parseInt(rawVersion.slice(-2))}`;
    return `${props.value} (${originalVersion})`;
  }
  throw new Error("Unknown humanize type -> " + props.type);
}

Humanize.propTypes = {
  type: PropTypes.oneOf([
    "time",
    "url",
    "size",
    "counter",
    "markdown",
    "original-version",
  ]).isRequired,
  value: PropTypes.any.isRequired,
  format: PropTypes.string,
  copyCode: PropTypes.string,
};

export default Humanize;

export function HumanizeCode({ code, lang }) {
  const [copyVisible, setCopyVisible] = useState(false);
  const [copied, setCopied] = useState(false);
  const language = hljs.getLanguage(lang) ? lang : "plaintext";

  const onCopy = () => {
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <div
      className="file-contents"
      onMouseOver={(e) => setCopyVisible(true)}
      onMouseOut={(e) => setCopyVisible(false)}
    >
      <div
        className={`position-relative float-end ${
          copyVisible ? "visible" : "invisible"
        }`}
      >
        <CopyToClipboard text={code} onCopy={onCopy}>
          <button
            type="button"
            className={`btn-copy-code btn btn-sm btn-outline-${
              copied ? "success" : "primary"
            }`}
            data-bs-toggle="tooltip"
            title="Click to Copy Code"
          >
            {copied ? "Copied!" : "Copy"}
          </button>
        </CopyToClipboard>
      </div>
      <pre className="p-0 m-0">
        <code
          className={`m-0 hljs language-${language}`}
          dangerouslySetInnerHTML={{
            __html: hljs.highlight(code, { language }).value,
          }}
        />
      </pre>
    </div>
  );
}

HumanizeCode.propTypes = {
  code: PropTypes.string.isRequired,
  lang: PropTypes.string,
};
