import { Link, useSearchParams } from "react-router-dom";
import { capitalize } from "../../helpers";

export default function SearchTypeButton() {
  const [searchParams] = useSearchParams();
  const currentType = searchParams.get("t") || "all";
  const _makeTypeUrl = (type) => {
    const qs = new URLSearchParams(searchParams);
    if (qs.has("p")) qs.delete("p");
    qs.set("t", type);
    return "/search?" + qs.toString();
  };

  return (
    <>
      <button
        className="btn btn-outline-secondary dropdown-toggle"
        type="button"
        id="searchTypeMenu"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        {capitalize(currentType)}
      </button>

      <ul
        className="dropdown-menu dropdown-menu-dark"
        aria-labelledby="searchTypeMenu"
      >
        {["all", "libraries", "platforms", "tools"].map((type) => (
          <li key={type}>
            <Link
              className={`dropdown-item ${
                currentType === type ? "active" : ""
              }`}
              to={_makeTypeUrl(type)}
            >
              {capitalize(type)}
            </Link>
          </li>
        ))}
      </ul>
    </>
  );
}
