import { useEffect, useRef, useState } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import Icon from "./../../components/icon";
import PropTypes from "prop-types";
import SearchTypeButton from "./type-button";

function SearchForm(props) {
  const navigate = useNavigate();
  const location = useLocation();
  const isAdvanced = props.type === "advanced";
  const isSearchPage = location.pathname.startsWith("/search");
  const [searchParams] = useSearchParams();
  const [inputValue, setInputValue] = useState(
    (isAdvanced ? searchParams.get("q") : "") || ""
  );
  const [searchFocused, setSearchFocused] = useState(false);
  const inputRef = useRef(null);

  const onDidSearch = (event) => {
    const qs = new URLSearchParams(searchParams);
    if (qs.has("p")) qs.delete("p");
    qs.set("q", inputValue);
    navigate(`/search?${qs.toString()}`);
    if (props.type === "navbar") {
      setInputValue("");
      inputRef.current.blur();
    }
    event.preventDefault();
  };

  const onDidKeyDown = (event) => {
    if (event.keyCode === 191 && inputRef.current) {
      inputRef.current.focus();
      setSearchFocused(true);
      event.preventDefault();
      document.removeEventListener("keydown", _kdListener);
    }
    if (event.keyCode === 27) {
      inputRef.current.blur();
    }
  };
  const _kdListener = onDidKeyDown.bind(this);

  useEffect(() => {
    if (isSearchPage && !isAdvanced) return;
    document.addEventListener("keydown", _kdListener);
    return function cleanup() {
      document.removeEventListener("keydown", _kdListener);
    };
  });

  const advancedForm = (
    <form role="search" onSubmit={(event) => onDidSearch(event)}>
      <div className="input-group input-group-lg mb-1">
        <SearchTypeButton />
        <input
          type="text"
          className="form-control"
          name="q"
          placeholder="Search for libraries, platforms or tools"
          aria-label="Search for libraries, platforms or tools"
          aria-describedby="button-search-submit"
          ref={inputRef}
          value={inputValue}
          onChange={(event) => setInputValue(event.target.value)}
          onFocus={() => setSearchFocused(true)}
          onBlur={() => setSearchFocused(false)}
        />
        <button
          className="btn btn-primary"
          type="submit"
          id="button-search-submit"
        >
          <Icon name="search" size="22" />
        </button>
      </div>
    </form>
  );

  const navbarForm = (
    <form
      role="search"
      className="pe-2"
      onSubmit={(event) => onDidSearch(event)}
    >
      <div
        className={`float-end position-relative ${
          searchFocused ? "invisible" : "visible"
        }`}
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="22"
          height="20"
          aria-hidden="true"
          className="position-absolute"
          style={{ right: "0.2rem", top: "0.3rem" }}
        >
          <path
            fill="none"
            stroke="#979A9C"
            opacity=".4"
            d="M3.5.5h12c1.7 0 3 1.3 3 3v13c0 1.7-1.3 3-3 3h-12c-1.7 0-3-1.3-3-3v-13c0-1.7 1.3-3 3-3z"
          ></path>
          <path fill="#979A9C" d="M11.8 6L8 15.1h-.9L10.8 6h1z"></path>
        </svg>
      </div>
      <input
        className={`form-control form-control-sm border-secondary ${
          searchFocused ? "" : "bg-dark text-white"
        }`}
        type="search"
        name="q"
        placeholder="Search..."
        aria-label="Search"
        ref={inputRef}
        value={inputValue}
        disabled={isSearchPage}
        onChange={(event) => setInputValue(event.target.value)}
        onFocus={() => setSearchFocused(true)}
        onBlur={() => setSearchFocused(false)}
      />
    </form>
  );

  return isAdvanced ? advancedForm : navbarForm;
}

SearchForm.propTypes = {
  type: PropTypes.oneOf(["advanced", "navbar"]),
};

export default SearchForm;
