import Icon from "./../../components/icon";
import { Modal } from "bootstrap";
import { makeSearchUrl } from "./../search/page";
import { useNavigate } from "react-router-dom";

export default function SearchExamplesModal() {
  const navigate = useNavigate();
  const makeSearch = (query) => {
    Modal.getInstance(document.getElementById("searchExamplesModal")).hide();
    navigate(makeSearchUrl(query));
  };
  return (
    <div
      className="modal fade"
      id="searchExamplesModal"
      tabIndex="-1"
      aria-labelledby="searchExamplesLabel"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="searchExamplesLabel">
              Syntax &amp; Examples
            </h5>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div className="modal-body">
            <table className="table table-hover table-striped">
              <thead>
                <tr>
                  <th>Search</th>
                  <th>Shows</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <div className="hstack gap-2 flex-wrap">
                      <div>
                        <button
                          onClick={() => makeSearch("ds18*")}
                          className="btn btn-outline-primary btn-sm"
                        >
                          <Icon name="search" /> DS18*
                        </button>
                      </div>
                      <div>
                        <button
                          onClick={() => makeSearch("pca*")}
                          className="btn btn-outline-primary btn-sm"
                        >
                          <Icon name="search" /> PCA*
                        </button>
                      </div>
                      <div>
                        <button
                          onClick={() => makeSearch("dht*")}
                          className="btn btn-outline-primary btn-sm"
                        >
                          <Icon name="search" /> DHT*
                        </button>
                      </div>
                      <div>
                        <button
                          onClick={() => makeSearch("sht*")}
                          className="btn btn-outline-primary btn-sm"
                        >
                          <Icon name="search" /> SHT*
                        </button>
                      </div>
                    </div>
                  </td>
                  <td>
                    <u>The asterisk</u> <code>*</code> serves as the truncation
                    (or wildcard) operator.
                    <br />
                    If you don't know the full name of a package, then use the
                    first 2-3 letters and <code>*</code> operator.
                  </td>
                </tr>
                <tr>
                  <td>
                    <button
                      onClick={() => makeSearch("framework:arduino i2c")}
                      className="btn btn-outline-primary btn-sm"
                    >
                      <Icon name="search" /> framework:arduino i2c
                    </button>
                  </td>
                  <td>
                    Arduino-compatible{" "}
                    <code>
                      I<sup>2</sup>C/TWI
                    </code>{" "}
                    packages
                  </td>
                </tr>
                <tr>
                  <td>
                    <button
                      onClick={() => makeSearch("platform:espressif32")}
                      className="btn btn-outline-primary btn-sm"
                    >
                      <Icon name="search" /> platform:espressif32
                    </button>
                  </td>
                  <td>
                    Packages compatible with <code>espressif32</code> platform
                  </td>
                </tr>
                <tr>
                  <td>
                    <button
                      onClick={() => makeSearch("keyword:web keyword:http")}
                      className="btn btn-outline-primary btn-sm"
                    >
                      <Icon name="search" /> keyword:web keyword:http
                    </button>
                  </td>
                  <td>
                    Filter by <code>web</code> and <code>http</code> keywords.
                  </td>
                </tr>
                <tr>
                  <td>
                    <button
                      onClick={() => makeSearch("sensor -temperature")}
                      className="btn btn-outline-primary btn-sm"
                    >
                      <Icon name="search" /> sensor -temperature
                    </button>
                  </td>
                  <td>
                    The all sensor libraries excluding temperature (
                    <code>-</code> operator)
                  </td>
                </tr>
                <tr>
                  <td>
                    <button
                      onClick={() =>
                        makeSearch("framework:mbed +(nRF24 HttpClient)")
                      }
                      className="btn btn-outline-primary btn-sm"
                    >
                      <Icon name="search" /> framework:mbed +(nRF24 HttpClient)
                    </button>
                  </td>
                  <td>
                    mbedOS-compatible <code>nRF24</code> or{" "}
                    <code>HttpClient</code> libraries. The search query can be
                    interpreted like{" "}
                    <code>mbed nRF24 OR energia HttpClient</code>
                  </td>
                </tr>
                <tr>
                  <td>
                    <button
                      onClick={() => makeSearch("header:RH_ASK.h")}
                      className="btn btn-outline-primary btn-sm"
                    >
                      <Icon name="search" /> header:RH_ASK.h
                    </button>
                  </td>
                  <td>
                    Search by header file (<code>#include</code>)
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="modal-footer">
            <a
              href="https://docs.platformio.org/en/latest/core/userguide/lib/cmd_search.html#description"
              target="_blank"
              rel="noopener noreferrer"
            >
              Advanced search syntax <Icon name="link-external" />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
