import { REGISTRY_API_ENDPOINT } from "./constants";
import axios from "axios";
import useSWR from "swr";

export async function dataFetcher(url) {
  if (!url.startsWith("http")) {
    url = REGISTRY_API_ENDPOINT + url;
  }
  const res = await axios.get(url);
  // console.debug("fetch result", url, res);
  return res.data;
}

function normalizePackageType(type) {
  if (type === "libraries") type = "library";
  if (type.endsWith("s")) type = type.slice(0, -1);
  return type;
}

export function usePackageData({ type, ownername, name, version }) {
  type = normalizePackageType(type);
  const { data, error } = useSWR(
    `/v3/packages/${ownername}/${type}/${name}${
      version ? `?version=${version}` : ""
    }`,
    {
      revalidateIfStale: false,
      revalidateOnFocus: !version,
      revalidateOnReconnect: !version,
    }
  );
  return {
    data: data,
    isLoading: !error && !data,
    isError: error,
  };
}

export function usePackageExamples({ type, ownername, name, version }) {
  type = normalizePackageType(type);
  const { data, error } = useSWR(
    `/v3/packages/${ownername}/${type}/${name}/examples${
      version ? `?version=${version}` : ""
    }`,
    {
      revalidateIfStale: false,
      revalidateOnFocus: !version,
      revalidateOnReconnect: !version,
    }
  );
  return {
    items: data,
    isLoading: !error && !data,
    isError: error,
  };
}

export function useExternalContents(url) {
  const { data, error } = useSWR(
    url,
    async (url) => (await axios.get(url, { transformResponse: [] })).data,
    {
      revalidateIfStale: false,
      revalidateOnFocus: false,
      revalidateOnReconnect: false,
    }
  );
  return {
    contents: data,
    isLoading: !error && !data,
    isError: error,
  };
}

export function useSearchResult({ query, page, sort, type }) {
  const qs = new URLSearchParams();
  const filters = [];
  if (query) filters.push(query.trim());
  if (type) {
    switch (type) {
      case "libraries":
        type = "library";
        break;
      case "platforms":
        type = "platform";
        break;
      case "tools":
        type = "tool";
        break;
      default:
        type = undefined;
    }
    if (type) filters.push(`type:"${type}"`);
  }
  if (filters.length) qs.set("query", filters.join(" "));
  if (sort && sort !== "relevance") qs.set("sort", sort);
  try {
    if (parseInt(page) > 1) qs.set("page", parseInt(page));
  } catch (err) {
    if (page) qs.set("page", page);
  }
  const qsStr = qs.toString();
  const { data, error } = useSWR(`/v3/search${qsStr ? `?${qsStr}` : ""}`);
  return {
    result: data,
    isLoading: !error && !data,
    isError: error,
  };
}
