import { Link, useSearchParams } from "react-router-dom";
import Icon from "./../../components/icon";
import Paginator from "paginator";
import PropTypes from "prop-types";

function SearchPagination(props) {
  const [searchParams] = useSearchParams();
  if (props.total <= props.resultsPerPage) return "";
  const _makePaginationUrl = (page) => {
    const qs = new URLSearchParams(searchParams);
    qs.set("p", page);
    return "/search?" + qs.toString();
  };
  const pagination = new Paginator(props.resultsPerPage, 7).build(
    props.total,
    props.currentPage
  );
  const pages = [];
  if (pagination.current_page > 6) {
    pages.push(...[1, 2, null]);
  }
  let p = pagination.first_page;
  while (p <= pagination.last_page) {
    pages.push(p);
    p++;
  }
  if (pagination.total_pages > pagination.current_page + 5) {
    pages.push(...[null, pagination.total_pages - 1, pagination.total_pages]);
  }

  return (
    <nav aria-label="Page navigation">
      <ul className="pagination justify-content-center">
        <li
          className={`page-item ${
            !pagination.has_previous_page ? "disabled" : ""
          }`}
        >
          <Link
            to={_makePaginationUrl(pagination.first_page)}
            className="page-link"
          >
            <Icon name="chevron-left" /> Previous
          </Link>
        </li>
        {pages.map((page, index) => (
          <li
            key={index}
            className={
              page === pagination.current_page
                ? "page-item active"
                : page
                ? "page-item"
                : "pt-1"
            }
          >
            {page ? (
              <Link to={_makePaginationUrl(page)} className="page-link">
                {page}
              </Link>
            ) : (
              "..."
            )}
          </li>
        ))}
        <li
          className={`page-item ${!pagination.has_next_page ? "disabled" : ""}`}
        >
          <Link
            to={_makePaginationUrl(pagination.next_page)}
            className="page-link"
          >
            Next <Icon name="chevron-right" />
          </Link>
        </li>
      </ul>
    </nav>
  );
}

SearchPagination.propTypes = {
  total: PropTypes.number.isRequired,
  currentPage: PropTypes.number.isRequired,
  resultsPerPage: PropTypes.number.isRequired,
};

export default SearchPagination;
