import { Link, useSearchParams } from "react-router-dom";
import Icon from "./../../components/icon";
import { capitalize } from "../../helpers";

export default function SearchSortButton() {
  const [searchParams] = useSearchParams();
  const currentSort = searchParams.get("s") || "relevance";
  const _makeSortUrl = (sort) => {
    const qs = new URLSearchParams(searchParams);
    if (qs.has("p")) qs.delete("p");
    qs.set("s", sort);
    return "/search?" + qs.toString();
  };
  return (
    <ul className="list-inline">
      <li className="list-inline-item me-1">
        <small className="text-muted">Search</small>
      </li>
      <li className="list-inline-item">
        <button
          type="button"
          className="btn btn-sm btn-link p-0"
          style={{ verticalAlign: "inherit" }}
          data-bs-toggle="modal"
          data-bs-target="#searchExamplesModal"
        >
          examples
        </button>
      </li>
      <li className="list-inline-item px-lg-1">|</li>
      <li className="list-inline-item me-1">
        <small className="text-muted">Sort by</small>
      </li>
      <li className="list-inline-item">
        <div className="dropdown">
          <button
            type="button"
            className="btn btn-sm btn-link p-0"
            id="searchSortMenu"
            data-bs-toggle="dropdown"
            aria-expanded="false"
            style={{ verticalAlign: "inherit" }}
          >
            {currentSort}
            <Icon name="triangle-down" />
          </button>

          <ul
            className="dropdown-menu dropdown-menu-dark"
            aria-labelledby="searchSortMenu"
          >
            {["relevance", "popularity", "trending", "maintenance"].map(
              (sort) => (
                <li key={sort}>
                  <Link
                    className={`dropdown-item ${
                      currentSort === sort ? "active" : ""
                    }`}
                    to={_makeSortUrl(sort)}
                  >
                    {capitalize(sort)}
                  </Link>
                </li>
              )
            )}
          </ul>
        </div>
      </li>
    </ul>
  );
}
