import Humanize from "../../components/humanize";
import SearchForm from "./form";
import SearchPagination from "./pagination";
import SearchResults from "./results";
import SearchExamplesModal from "./examples-modal";
import SearchSortButton from "./sort-button";
import Spinner from "../../components/spinner";
import { useSearchParams } from "react-router-dom";
import { useSearchResult } from "./../../data";

export default function SearchPage() {
  const [searchParams] = useSearchParams();
  const searchQuery = searchParams.get("q");
  const searchType = searchParams.get("t");
  const searchSort = searchParams.get("s");
  const { result, isLoading, isError } = useSearchResult({
    query: searchQuery,
    page: searchParams.get("p"),
    sort: searchSort,
    type: searchType,
  });
  if (isLoading || isError) return <Spinner />;
  return (
    <>
      <div className="container justify-content-center">
        <div className="row">
          <div className="col-0 col-lg-1"></div>
          <div className="col col-lg-10">
            <SearchForm type="advanced" />
            <div className="d-flex">
              <div className="flex-fill text-muted">
                <small>
                  Found <Humanize type="counter" value={result.total} />{" "}
                  packages
                </small>
              </div>
              <div>
                <SearchSortButton />
              </div>
            </div>
          </div>
          <div className="col-0 col-lg-1"></div>
        </div>
      </div>
      <SearchExamplesModal />
      {result.total ? (
        <>
          <hr />
          <SearchResults items={result.items} />
          <SearchPagination
            total={result.total}
            currentPage={result.page}
            resultsPerPage={result.limit}
          />
        </>
      ) : (
        <div className="text-center pt-4">
          <h5 className="card-title">Nothing has been found by your request</h5>
          <p className="card-text">
            Try a less-specific search or use truncation (or wildcard) operator{" "}
            <code>*</code>
          </p>
          <button
            type="button"
            className="btn btn-primary"
            style={{ verticalAlign: "inherit" }}
            data-bs-toggle="modal"
            data-bs-target="#searchExamplesModal"
          >
            Search Examples
          </button>
        </div>
      )}
    </>
  );
}

export function makeSearchUrl(query, page) {
  if (!query && !page) return "/search";
  const qs = new URLSearchParams();
  if (query instanceof Object) {
    const filterArr = [];
    for (const [key, value] of Object.entries(query)) {
      filterArr.push(`${key}:"${value}"`);
    }
    qs.set("q", filterArr.join(" "));
  } else {
    qs.set("q", query);
  }
  if (page) qs.set("p", page);
  return "/search?" + qs.toString();
}
